es-list__item--info__details span{font-weight:600}.single-country{padding:20px;margin-top:50px}.single-country__container{align-items:center;display:flex;flex-direction:column;justify-content:space-between;margin-top:50px}.single-country__container--flag img{max-width:350px}@media screen and (min-width: 1024px){.single-country__container--flag img{max-width:650px}}.single-country__container--details{align-content:center;display:flex;margin-top:20px;max-width:350px;flex-wrap:wrap;justify-content:space-between}.single-country__container--details h2{font-size:28px;font-weight:800;margin-bottom:10px;width:100%}.single-country__container--details .detail{margin:5px 0;min-width:250px}.single-country__container--details .detail-title{font-weight:600}.single-country__container--details .detail.languages{width:100%}.single-country__container--details .borders-countries{display:flex;flex-direction:column;justify-content:space-between;margin-top:25px}.single-country__container--details .borders-countries span{font-weight:600;height:15px}.single-country__container--details .borders-countries__list{display:flex;flex-wrap:wrap}.single-country__container--details .borders-countries__list--item{margin:2px 5px}@media screen and (min-width: 1024px){.single-country__container--details .borders-countries{align-items:center;flex-direction:row}}@media screen and (min-width: 1024px){.single-country__container--details{margin:0 100px;max-width:600px}}@media screen and (min-width: 1024px){.single-country__container{flex-direction:row}}.link-btn{border-radius:3px;-ms-box-shadow:0 1px 2px #858585;box-shadow:0 1px 2px #858585;cursor:pointer;height:28px;align-items:center;text-align:center;line-height:28px;padding:0 5px}.link-btn ion-icon{margin:0 5px;vertical-align:sub}.darkmode .link-btn{background-color:#2b3945}.search-filter-container{margin:50px 0;padding:0 25px;display:flex;flex-direction:column}@media screen and (min-width: 1024px){.search-filter-container{align-items:center;flex-directi